















import { USER_LIST, UserBasicInfo } from "@/constant/users";
import { groupBy, keys } from "lodash";

export default {
  name: "UserList",
  data() {
    const groupedUsers = groupBy(USER_LIST, (user: UserBasicInfo) => user.id.substr(0, 1));
    return {
      firstLetters: keys(groupedUsers),
      groupedUsers,
    };
  }
};

